.footer {
  background-color: @dimgray;

  &__inner {
    display: flex;
  }

  &__left {
    background-color: @red;
    padding: 38px 52px;
  }

  &__center {
    background-color: black;
  }

  &__right {
    display: flex;
    flex-grow: 1;
    padding: 52px 50px 0 65px;
  }

  &__logo {
    display: block;
    .fade();
  }

  &__logo img {}

  &__contacts {
    color: white;
  }

  &__social-list {
    padding: 18px 45px;
  }

  &__social-item {
    margin-top: 23px;
  }

  &__social-link {
    display: block;
    filter: brightness(0) invert(1);
    transition: filter 0.5s;

    img {
      display: block;
    }

    &:hover,
    &:focus {
      filter: brightness(1) invert(0);
    }
  }

  &__left-links {}

  &__right-links {
    margin-left: auto;
    text-align: right;
  }

  &__link {
    display: block;
    color: white;
    text-decoration: none;
    transition: color 0.3s;

    &:hover,
    &:focus {
      color: @darkred;
      text-decoration: underline;
    }
  }

}
